import { css } from '@emotion/react'
import { mediaStyle } from './theme'
import TTMedium from '../../../fonts/TT-Norms-Pro-Medium.otf';
import TTBold from '../../../fonts/TT-Norms-Pro-Bold.otf';
import TTRegular from '../../../fonts/TT-Norms-Pro-Regular.otf';
import FlamaBasic from '../../../fonts/FlamaSemicondensed-Basic.otf';
import FlamaBold from '../../../fonts/FlamaCondensed-Bold.otf';

export default css`
  html {
    height: 100%;
  }
  body {
    min-width: 100%;
    height: 100%;
  }

  @font-face {
    font-family: TT-Norms;
    src: url("${TTMedium}"),
    url("${TTBold}"),
    url("${TTRegular}");
  }

  @font-face {
    font-family: Flama;
    src: url("${FlamaBasic}"),
    url("${FlamaBold}");
  }

  @font-face {
    font-family: TT-Norms;
    src: url("${TTMedium}"),
    url("${TTBold}"),
    url("${TTRegular}");
  }

  #___gatsby {
    height: 100%;
    > div,
    .tl-edges,
    .tl-wrapper {
      height: 100%;
    }
  }

  img {
    width: 100%;
  }

  ${mediaStyle}
`;
