import React from 'react';
import { max, set } from 'lodash';
import { navigate } from 'gatsby';
import { Formik } from 'formik'
import score from '../../json/score';

const Provider = ({ children }) => {
  const calculateScore = Object.values(score)
  const citys = Object.keys(score)
  return (
    <Formik
      initialValues={{ answers: [] }}
      onSubmit={async (values) => {
        const totolPoints = await calculateScore.reduce((res, scores) => {
          const point = scores.reduce((total, num, i) => {
            total = total + (num[values.answers[i]] || +'0')
            return total
          }, 0)
          res.push(point)
          return res
        },[])
        const cityIndexs = await totolPoints.reduce((res, num, i) => {
          if (num === max(totolPoints)) {
            res.push(i)
          }
          return res
        }, [])
        const city = await citys[cityIndexs[Math.floor(Math.random()*cityIndexs.length)]]
        set(values, ['points'], max(totolPoints))
        navigate(`/result/${city}`)
      }}
    >
      {children}
    </Formik>
  )
}

export default Provider;
